.load {
  position: fixed;
  left: 45%;
  top: 35%;
 }
.divLoading {
  position: fixed;
  left:0;
  top:0;
  width:100%;
  height:100%;
  background-color:rgba(0, 0, 0, 0.3);
  text-align: center
}
.root {
  max-width: 100vw;
  overflow-x: scroll;
  overflow-y: hidden;
  position: relative;
}

.timelineContainer {
  position: relative;
  background-color: #f6f6f8;
  display: -ms-flexbox;
  display: flex;
}

.rowsContainer {
  position: relative;
  left: 81px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.rowContainer {
  display: -ms-flexbox;
  display: flex;
}

.buttonsContainer {
  position: absolute;
  z-index: 4;
  display: -ms-flexbox;
  display: flex;
  right: 32px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.loader {
  width: 70vw;
  position: absolute;
  margin: 0 auto;
}



