.formContainer {
  margin: 2rem 0;
}

.label {
  width: 100%;
  border-bottom: 1px solid lightgrey;
  display: block;
  font-size: 2em;
}

.dateInstr {
  font-size: 15px;
  color: grey;
  font-style: italic;
  margin-left: 4px;
}

.buttonsContainer {
  margin-top: 6rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: right;
      align-items: right;
}

.select,
input[type='date'] {
  width: 300px;
  height: 30px;
  padding: 8px;
  margin-bottom: 2rem;
  margin-top: 1rem;
  font-size: 20px;
}

.btn {
  border: none;
  min-width: 120px;
  padding: 20px;
  margin-left: 20px;
  border-radius: 10px;
  font-size: 1.5em;
  text-align: center;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.btn:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}

.btn.ok {
  background: green;
}

.btn.cancel {
  background: red;
}

.errorMessage {
  color: red;
}



