.modalToggler {
  border: none;
  margin: none;
  padding: 10px;
  color: white;
  background-color: rgb(63, 81, 181);
  float: right;
  margin: 0 1rem 1rem 0;
  cursor: pointer;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.modalToggler:active {
  -webkit-transform: scale(0.6);
      -ms-transform: scale(0.6);
          transform: scale(0.6);
}

.modal {
  width: 50vw;
}

.loader {
  display: block;
  margin: 0 auto;
}

.tabs{display: inline-block;
  z-index: 10;
  color:#606161;
  line-height: 13px;
  margin: 10px 10px 10px 5%;
  height: 35px;
}

.ReadmeTab{float: left;
  display: block;
  margin: 5px 0 10px 5px;
  padding: 10px;
  background-color: white;
  border: 1px solid rgb(224, 223, 223);
  border-right: 0;
  cursor: pointer;
  height: 35px;
  border-radius: 17.5px 0 0 17.5px;
}

.AttendanceTab{float: left;
  display: block;
  margin: 5px 10px 5px 0;
  padding: 10px;
  background-color: white;
  border: 1px solid rgb(224, 223, 223);
  cursor: pointer;
  height: 35px;
  border-radius: 0 17.5px 17.5px 0;
}

.AttendanceTab:hover{ -webkit-box-shadow:  0px 3px 18px 1px rgba(0,0,0,0.1); box-shadow:  0px 3px 18px 1px rgba(0,0,0,0.1);
  background-color: rgb(226, 224, 224);
}

.ReadmeTab:hover{  -webkit-box-shadow:  0px 3px 18px 1px rgba(0,0,0,0.1);  box-shadow:  0px 3px 18px 1px rgba(0,0,0,0.1);
  background-color: rgb(226, 224, 224);
}






