.formWrapper {
  margin: 2rem 0;
  position: relative;
}

.label {
  margin-bottom: 1rem;
  margin-top: 2rem;
  width: 100%;
  border-bottom: 1px solid lightgrey;
  display: block;
  font-size: 2em;
}

.select {
  min-width: 300px;
  min-height: 30px;
  padding: 8px;
  margin-bottom: 2rem;
  margin-top: 1rem;
  font-size: 20px;
}

.assignedTeacherWrapper {
  background: lightblue;
  font-size: 1.5em;
  padding: 10px;
  border-radius: 12px;
}

.unAssignTeacher {
  font-size: 2.2rem;
  color: red;
  margin-right: 10px;
  cursor: pointer;
}

.btnWrapper {
  margin-top: 6rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: right;
      align-items: right;
}

.btn {
  border: none;
  min-width: 120px;
  padding: 20px;
  margin-left: 20px;
  border-radius: 10px;
  font-size: 1.5em;
  text-align: center;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.btn:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}

.btn.ok {
  background: green;
}

.btn.cancel {
  background: red;
}



